.header {
  padding-top: rem(20);
  padding-bottom: rem(20);
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  z-index: 100;

  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__logo {
    @include sizerem(98); //sizerem(width,[height])
    img {
      display: block;
      width: 100%;
    }
  }

  &__btns-n-link {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  &__close {
    display: none;
  }

  &__phone {
    font-family: $roboto-font;
    white-space: nowrap;
    @include fontrem(20, 23.44, 600);
    margin-right: rem(20);
  }

  &__lang {
    display: none;
  }

  &__login {
    margin-right: rem(10);
  }

  &__reg {
    height: rem(50);
  }

  &__burger {
    display: none;
  }

  &__navigation {
    flex: 1 1 auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__nav-list {

  }
  @media screen and (max-width: $desk-sm-width) {
    &__phone {
      margin-right: rem(15);
    }
  }
  @media screen and (max-width: $tab-width) {
    &__container {
      padding-left: rem($padding-container);
      padding-right: rem($padding-container);
    }

    @keyframes hidemenu {
      0% {
        opacity: 0;        
      }
      50% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
    }

    &__navigation {
      position: fixed;
      left: 0;
      top: 0;
      bottom: 0;
      width: 16rem;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      background-color: $back-default-color;
      padding: rem($padding-container-tab);
      transform: translateX(-100%);
      z-index: 100;
      animation-name: hidemenu;
      animation-duration: $animate-time * 2;
      animation-fill-mode: forwards;
      animation-timing-function: ease-in-out;
      animation-iteration-count: 1;
      transition: transform $animate-time ease-in-out;

      &.active {
        transform: translateX(0);
        box-shadow: 0 0 rem(20) rgba(0,0,0,0.7);
      }
    }
    &__lang {
      display: flex;
      margin-right: rem(20);
    }
    &__burger {
      display: flex;
    }
    &__login {
      margin-right: rem(20);
      padding: 0 rem(30);
    }
    &__phone {
      margin-right: rem(20);
      padding: 0 rem(30);
      letter-spacing: 0.012em;
    }
    &__nav-list {
      width: 100%;      
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      margin-bottom: rem(40);
    }
    &__close {
      display: block;
      margin-bottom: rem(40);
      margin-left: auto;
    }
    &__login {

    }
    &__reg {
      width: 100%;
    }
  }
  @include max(630) {
    padding-top: rem(8);
    padding-bottom: rem(8);
    &__logo {
      @include size(48, 42); //sizerem(width,[height])
    }
    &__phone {
      padding: 1rem;
      margin-right: 1rem;
    }
    &__lang {
      margin-right: 1rem;
    }
    &__login {
      margin-right: 1rem;
    }
  }
  @media screen and (max-width: $xs-mob-width) {
    &__lang {
      margin-left: rem(10);
      margin-right: rem(10);
    }
    &__phone {
      margin-right: 0;
      padding: 0 rem(8);
      letter-spacing: -0.01em;
      @include fontrem(16, 18.78, 500);
    }
    &__btns-n-link {
      position: relative;
      top: rem(-2);
    }
    &__login {
      border: 1px solid #000;
      margin-bottom: rem(20);
      width: 100%;
    }
  }
}
