.form-place {
  padding: rem(120) 0;
  display: flex;
  overflow: hidden;
  min-height: rem(1101);
  position: relative;
  &__container {
    padding-left: rem(40);
    padding-right: rem(40);
  }
  &__inner {
    max-width: 100%;
    width: rem(1140);
    margin: auto;
    position: relative;
    z-index: 2;
    background-color: $back-default-color;
    overflow: hidden;
  }
  &__bg {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
  }
  @media screen and (max-width: $tab-width) {
    padding: rem(40) 0;
    min-height: 0;
    &__bg {
//      object-position: 60% 60%;
    }
  }
  @media screen and (max-width: $xs-mob-width) {
    padding: rem(20) 0;
    &__container {
      padding-left: rem(20);
      padding-right: rem(20);
    }
  }
}
