.lang {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 rem(8);
  &__item {
    display: flex;
    margin-right: rem(8);
    &:last-child {
      margin-right: 0;
    }
   &:not(.lang__item--active) {    
    .lang__link {
      color: $text-disabled-color;
      &:hover {
        text-shadow: none;
      }      
    }
   }
  }
  @media screen and (max-width: $xs-mob-width) {
    &__item {
      margin-right: .4rem;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}