.nav-list {
  @include no-list;
  display: flex;
  justify-content: center;
  align-items: center;
  &__item {
    margin-right: rem(40);
    display: flex;
    &:last-child {
      margin-right: 0;
    }
  }
  &__link {

  }
  @media screen and (max-width: $tab-width) {
    &__item {
      margin-bottom: rem(20);
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
