.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: none;
  background: none;
  padding: 0;
  cursor: pointer;
  text-decoration: none;
  border-radius: rem(12);
  transition: all $animate-time ease-in-out;
  white-space: nowrap;
  &:hover {
    text-shadow: 0 0 .03em currentColor, 0 0 .03em currentColor;
  }
  &__icon {
    @include sizerem(24, 24); //sizerem(width,[height])
    margin-left: rem(10);
  }
  &--size {
    &--medium {
      height: rem(48);
      padding: 0 rem(30);      
      @include fontrem(16, 18.78, 600);
      @media screen and (max-width: $desk-sm-width) {
        padding: 0 rem(25);
      }
    }
    &--big {
      height: rem(65);
      padding: 0 rem(34);
      @include fontrem(18, 21.13, 600);
    }
  }

  &--color {
    &--transp {
      background-color: transparent;
      color: $text-default-color;
    }
    &--default {
      background-color: $default-btn-color;
      color: $text-default-color;
    }
    &--light {
      background-color: $light-btn-color;
      color: $text-default-color;
    }
  }

  &--border {
    border: 1px solid #000;
  }
}
