.begin-work {
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  display: flex;
  padding: rem(126) 0;
  text-align: center;

  &__bg {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    position: absolute;
    top: 0;
    left: 0;
  }

  &__container {
    margin: auto;
    position: relative;
  }

  &__title {
    font-family: $marmelad-font;
    @include fontrem(56, 66.7, 400);
    max-width: rem(887);
    margin: 0 auto rem(40);
  }

  &__title-item {

   &--other {

   }

   &--mob {
    display: none;
   }
  }

  &__text {
    @include fontrem(26, 30.52, 300);
    max-width: rem(700);
    margin: 0 auto rem(58);
  }

  &__btns-place {
    display: flex;
    justify-content: center;   
  }

  &__btn {
    &--begin-sell {
      margin-left: rem(20);
      min-width: rem(242);
    }
  }
  @media screen and (max-width: $tab-width) {
    padding-top: 6.24rem;
    &__title {
      line-height: 1.21em;
    }
  }
  @media screen and (max-width: $xs-mob-width) {
    padding: rem(58) 0;
    &__title {
      @include fontrem(24, 28.58);
      margin-bottom: rem(24);
      max-width: rem(320);
    }
    &__title-item {

      &--other {
        display: none;
      }

      &--mob {
        display: inline;
      }
    }

    &__text {
      @include fontrem(16, 18.78);
      margin-bottom: rem(24);
    }
    &__btn {
      height: rem(43);
      @include fontrem(16, 18.78);      
      padding: 0 rem(5);
      flex: 1 1 50%;
      &--begin-sell {
        min-width: rem(0);
        margin-left: rem(8);
      }
    }
  }
}