.footer {
  padding: rem(80) 0;
  background-color: $text-default-color;
  color: $back-default-color;
  &__container {
    display: grid;
    grid-template-columns: perc(816, 1420) 1fr auto;
    grid-template-rows: auto;
  }

  &__our-offices {
    padding-right: rem(20);
    a {
      color: inherit;
      text-decoration: none;
    }
  }

  &__title {
    font-family: $marmelad-font;
    white-space: nowrap;
    @include fontrem(24, 28.58, normal);
    margin-bottom: rem(24);
    flex: 0 0 100%;
  }

  &__in-socio {
    padding-right: rem(20);
  }

  &__copy {
    display: none;
    @include fontrem(12, 14.09, 400);
  }

  &__list {
    @include no-list;    
  }

  &__list-item {
    margin-bottom: rem(16);
    &:last-child {
      margin-bottom: 0;
    }
  }



  @media screen and (max-width: $desk-sm-width) {
    &__container {
      grid-template-columns: 1fr 1fr auto;
    }
  }

  @media screen and (max-width: $tab-width) {
    padding: rem(32) 0;
    &__container {
      grid-template-columns: 1fr auto;
      grid-template-rows: auto;
      row-gap: rem(48);
    }
    &__our-offices {
      grid-column: 1 / 2;
      grid-row: 1 / 2;
    }
    &__in-socio {
      grid-column: 1 / 2;
      grid-row: 2 / 3;
    }
    &__copy {
      display: block;
      grid-column: 1 / 2;
      grid-row: 3 / 4;
    }
    &__contacts {
      grid-column: 2 / 3;
      grid-row: 1 / 4;
      min-width: 0;
    }

  }
  @media screen and (max-width: $tab-width) and (min-width: 630px) {
    &__btn {
      min-width: rem(247);
      max-width: 100%;
    }
  }
  @media screen and (max-width: $xs-mob-width) {
    &__container {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      gap: 0;
    }
    &__copy {
      order: 4;
    }
    &__our-offices, &__in-socio, &__contacts {
      padding-bottom: rem(48);
    }
    &__btn {
      min-width: rem(172);
    }
  }
}
