.reg-form {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  gap: rem(20);
  &__item {
   &--btns-place {
    padding-top: rem(20);
   }
   &--confirm {
    margin-top: rem(20);
   }
   &--btns-place-step-2 {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
   }
  }
  &__label {
    display: block;
    @include fontrem(14, 16.44, 500);
    margin-bottom: rem(10);
  }

  &__input[type='email'], &__input[type='tel'], &__input[type='number'], &__input[type='password'], &__input[type='text'], &__input[type='date'] {    
    display: block;
    line-height:1;
    width: 100%;
    box-sizing: border-box;
    font-family: inherit;
    height: rem(48);
    padding: 0 rem(8);
    font-size: rem(14);
    border-radius: rem(6);
    transition: all $animate-time ease-in-out;
    color: inherit;
    background-color: $default-input-color;
    border: 1px solid $default-input-color;
    &.js-error {
      border-color: #c00909;
      box-shadow: 0 0 0.5rem #c00909;
    }
    @include placeholder(#8E825C,1);
  }

  &__error {
    @include fontrem(12, 14);
    color: #c00909;
    margin-top: rem(3);
  }

  //

  &__btns-row {
    display: flex;
    justify-content: space-between;
  }

  &__captcha {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #A0B2AF;
    border-radius: rem(12);
    padding: rem(20);
    img {
      display: block;
      max-width: 100%;
      @include sizerem(254); //sizerem(width,[height])
    }
  }

  &__btn {
    height: rem(51);
   &--submit-wide {
    width: 100%;
   }
   &--back {
    flex: 0 0 rem(128);
    margin-right: rem(9);
   }

   &--submit {
    flex: 0 0 rem(183);
   }
  }
  @media screen and (max-width: $tab-width) and (min-width: 631px) {
    grid-template-columns: 1fr 1fr;    
    column-gap: rem(40);
    &__item {

      &--name {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
      }

      &--company {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
      }

      &--email {
        grid-column: 1 / 2;
        grid-row: 3 / 4;
      }

      &--phone {
        grid-column: 2 / 3;
        grid-row: 1 / 2;
      }

      &--code {
        grid-column: 2 / 3;
        grid-row: 2 / 3;
      }

      &--btns-place {
        grid-column: 2 / 3;
        grid-row: 3 / 4;
        align-items: flex-end;
        padding-top: rem(24);
      }

      &--passw {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
      }

      &--confpassw {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
      }

      &--captcha-img {
        grid-column: 2 / 3;
        grid-row: 1 / 2;
      }

      &--captcha-field {
        grid-column: 1 / 2;
        grid-row: 3 / 4;
      }

      &--btns-place-step-2 {
        grid-column: 2 / 3;
        grid-row: 2 / 4;
      }

    }
    &__btn {
      &--submit {
        flex: 1 1 auto;
      }
      &--back {
        flex: 0 0 perc(105, 292);
        margin-right: rem(8);
      }
    }
    &__captcha {
      padding-top: rem(10);
      padding-bottom: rem(10);
      height: rem(94);
      margin-bottom: rem(-20);
    }
  }
  @media screen and (max-width: $xs-mob-width) {
    &__btn {
      &--submit {
        flex: 1 1 auto;
      }
      &--back {
        flex: 0 0 perc(105, 292);
        margin-right: rem(9);
      }
    }
    &__captcha {
      padding: rem(10) rem(12);
      height: rem(94);
    }
    &__item {
      &--btns-place-step-2 {
        padding-top: rem(4);
      }
    }
    &__item--confirm {
      //margin-top: rem(18);
    }
  }
}
