.form-n-text {
  display: flex;
  padding: rem(80);

  &__bg {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 1;
  }
  &__text-place {
    flex: 1 1 auto;
    position: relative;
    z-index: 2;
  }

  &__title {
    font-family: $marmelad-font;
    @include fontrem(36, 42.88, 400);
    margin-bottom: rem(40);
  }

  &__description {
    @include fontrem(18, 21.13);
    margin-bottom: rem(21.13);
  }

  &__fields-place {
    flex: 0 0 rem(320);
    margin-left: rem(80);
    position: relative;
    z-index: 2;
  }
  @media screen and (max-width: $desk-sm-width) {
    padding: rem(40);
  }
  @media screen and (max-width: $tab-width) {
    padding: rem(32);
    display: block;
    &__text-place {
      margin-bottom: rem(58);
    }
    &__fields-place {
      margin-left: 0;
    }
    &__bg {
      display: none;
    }
  }
  @media screen and (max-width: $xs-mob-width) {
    padding: rem(20);    
    &__title {
      @include fontrem(24, 28.58);
      margin-bottom: rem(32);
    }
    &__description {
      @include fontrem(16, 18.98);
      margin-bottom: rem(18.78);
    }
    &__text-place {
      margin-bottom: rem(32);
    }
  }
}
