.header-link {
  @include fontrem(18, 21.13, 500);
  letter-spacing: .012em;
  color: $text-default-color;
  text-decoration: none;
  transition: text-shadow $animate-time ease-in-out;
  &:hover {
    text-shadow: 0 0 em(.6, 18) currentColor, 0 0 em(.6, 18) currentColor;
  }
  @media screen and (max-width: $tab-width) {
    letter-spacing: .045em;
  }
  @media screen and (max-width: $xs-mob-width) {
    @include fontrem(16, 18.78);
  }
}