.our-offices {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  flex-wrap: wrap;
  &__item {
    margin-right: rem(40);
    //min-width: rem(275);
    white-space: nowrap;
    flex: 0 1 min-content;
    @include fontrem(18, 21.13, 500);
    &:last-child {
      margin-right: 0;
    }
    @media screen and (max-width: $desk-sm-width) {
      margin-bottom: rem(16);
      &:last-child {
        margin-bottom: 0;
      }
    }
    @include max(630) {
      margin-right: 0;
    }

  }
}
